@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap");

@import url(./reset.css);

/* 숨기기 */
.a11y-hidden {
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  width: 1px;
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
}

/* 변수 */
:root {
  --white: #fff;
  --pink: #ff0060;
  --bg: #090424;
}

/*reset*/
* {
  box-sizing: border-box;
}

img,
svg {
  vertical-align: top;
}

ol,
ul,
dl,
li {
  padding: 0;
  margin: 0;
  list-style: none;
}

button {
  padding: 0;
  margin: 0;
}

.App {
  height: 100vh;
}

/* style */
body {
  font-family: "Roboto", sans-serif;
}

button {
  min-width: 50px;
  min-height: 50px;
  cursor: pointer;
}

@media screen and (max-width: 767px) {
  button {
    min-width: 40px;
    min-height: 40px;
  }
}

@media screen and (max-width: 480px) {
  button {
    min-width: 30px;
    min-height: 30px;
  }
}

/* 배경이미지 */
.bg {
  height: 100vh;
  position: relative;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;

  font-family: "Roboto", sans-serif;
}

.bg::before {
  content: "";
  background: rgba(70, 70, 70, 0.3);
  backdrop-filter: blur(15px);
  position: absolute;
  width: 100%;
  height: 100%;
}

/* 타임라인, 볼륨바 커스텀 스타일 */
input[type="range"] {
  -webkit-appearance: none;
  appearance: none;
}

input[type="range"]:focus {
  outline: none;
}

input[type="range"]::-webkit-slider-runnable-track {
  height: 100%;
  background-color: transparent;
  cursor: pointer;
  border-radius: 10px;
}

input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 16px;
  width: 16px;
  border-radius: 50%;
  background: var(--pink);
  cursor: pointer;
  margin-top: -5px;
  box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.5);
}

/* 스크롤바 커스텀 */

*::-webkit-scrollbar {
  width: 7px;
  height: 5px;
}

*::-webkit-scrollbar-thumb {
  background-color: #888;
  border-radius: 10px;
}

*::-webkit-scrollbar-thumb:hover {
  background-color: #555;
}

*::-webkit-scrollbar-track {
  background-color: #f5f5f5;
  border-radius: 10px;
}
